<template>
  <div class="exchange-box">
     {{ address }}
    <div class="exchange-top">
      <div class="select-box flex-middle van-hairline--bottom">
        <div class="select-item flex-item">
          <img class="coin-icon" :src="coin1.logo" alt="" />
          <Popover
            v-model:show="showLeftPopover"
            :actions="coinsLeft"
            @select="onLeftSelect"
          >
            <template #reference>
              <div class="coin-name-box flex-middle">
                <span class="name">{{ coin1.mainchain_name }}</span>
                <img
                  class="select-icon"
                  src="@/assets/images/exchange/select_icon.png"
                  alt=""
                />
              </div>
            </template>
          </Popover>
        </div>
        <img
          class="exchange-icon"
          src="@/assets/images/exchange/tran-exchange.png"
          alt=""
        />
        <div class="select-item flex-item">
          <img class="coin-icon" :src="coin2.logo" alt="" />
          <Popover
            v-model:show="showRightPopover"
            :actions="coinsRight"
            @select="onRightSelect"
          >
            <template #reference>
              <div class="coin-name-box flex-middle">
                <span class="name">{{ coin2.mainchain_name }}</span>
                <img
                  class="select-icon"
                  src="@/assets/images/exchange/select_icon.png"
                  alt=""
                />
              </div>
            </template>
          </Popover>
        </div>
      </div>
      <div class="field-box flex-between">
        <Field
          :border="false"
          type="number"
          class="flex-item"
          v-model="coin1Amount"
          placeholder=""
        />
        <Field
          input-align="right"
          type="number"
          readonly
          :border="false"
          class="flex-item"
          v-model="coin2Amount"
          placeholder=""
        />
      </div>
      <div class="balance van-hairline--top">:{{ currentBalance }}</div>
      <Field
        v-if="showAddressInput"
        v-model="toAddress"
        placeholder=""
      />
    </div>
    <div class="tips-box">
      <div class="item">:{{ coin2.ratio }}</div>
      <div class="item">:{{ coin2.fee }}{{ coin2.symbol }}</div>
    </div>
    <Button round type="primary" @click="postInsert" size="large"></Button>
  </div>
</template>

<script>
import { reactive, toRefs, onMounted, computed } from "vue";
import { Field, Button, Popover, Toast } from "vant";
import { useStore } from "vuex";
import { getCoinSrcList, getCoinDstList, postInsert } from "@/api";
export default {
  components: {
    Field,
    Button,
    Popover
  },
  setup() {
    const store = useStore();
    const data = reactive({
      coin1Amount: "",
      coin2Amount: computed(() => {
        if (data.coin1Amount && data.coin2) {
          let ratioArr = data.coin2.ratio.split(":");
          return (data.coin1Amount * ratioArr[1]) / ratioArr[0];
        }
        return "";
      }),
      toAddress: "",
      showAddressInput: computed(() => {
        if (
          data.coin1 &&
          data.coin2 &&
          data.coin1.mainchain_symbol !== data.coin2.mainchain_symbol
        ) {
          return true;
        }
        return false;
      }),
      address: computed(() => store.state.accounts[0]),
      provider: computed(() => store.state.provider),
      currentBalance: computed(() => store.state.currentBalance),
      coin1: {},
      coin2: {},
      coinsLeft: [],
      coinsRight: [],
      showLeftPopover: false,
      showRightPopover: false,
      onLeftSelect: item => {
        data.coin1 = item;
        data.getCoinList(item.id);
      },
      onRightSelect: item => {
        data.coin2 = item;
      },
      getCoinList: id => {
        getCoinDstList({ coin_id: id }).then(res => {
          let arr = res.data.data.map(item => {
            return { ...item, text: item.mainchain_name };
          });
          data.coinsRight = arr;
          data.coin2 = arr[0];
        });
      },
      postInsert: () => {
        if (!data.address) {
          Toast("");
          return;
        }
        if (!data.toAddress && data.showAddressInput) {
          Toast("");
          return;
        }
        postInsert({
          src_coin_id: data.coin1.id,
          src_address: data.address,
          dst_coin_id: data.coin2.id,
          dst_address: data.showAddressInput ? data.toAddress : data.address,
          src_number: data.coin1Amount
        }).then(res => {
          let address = res.data;
          data.provider.eth
            .sendTransaction({
              from: data.address,
              to: address,
              value: data.provider.utils.toWei(data.coin1Amount)
            })
            .then(result => {
              console.log(result);
            });
        });
      }
    });
    onMounted(() => {
      getCoinSrcList().then(res => {
        console.log(res);
        let arr = res.data.data.map(item => {
          return { ...item, text: item.mainchain_name };
        });
        data.coinsLeft = arr;
        data.coin1 = arr[0];
        data.getCoinList(data.coin1.id);
      });
    });
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>
<style lang="less">
.exchange-top .van-cell::after {
  left: 0;
  right: 0;
}
</style>
<style lang="less" scoped>
.exchange-box {
  padding: 12px;
  text-align: left;
}
.exchange-top {
  background: #fff;
  border-radius: 6px;
  padding: 10px;
  .select-box {
    margin-bottom: 15px;
  }
  .select-item {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 10px 0 20px;
    .coin-icon {
      width: 30px;
      height: 30px;
      margin-bottom: 7px;
    }
    .name {
      color: #111;
      font-size: 15px;
    }
    .select-icon {
      width: 12px;
    }
  }
  .exchange-icon {
    width: 40px;
    margin: 0 20px;
  }
  .balance {
    text-align: left;
    padding: 10px;
    color: #3377ff;
    font-size: 13px;
  }
}
.tips-box {
  color: #666666;
  margin-bottom: 20px;
  font-size: 13px;
  padding: 10px;
  line-height: 24px;
}
</style>
